body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.clickable{
	cursor: pointer;
}

.active{
	background: #ffffff;
}

.fixed-div{
	position: fixed;
	width: 25vw;
	margin: auto;
	z-index: 1;
}

.scroller {
	height: 100vh;
	overflow: auto;
}

/* Profile container */
.profile {
	margin: 15vh auto;
	width: 80%;
	height: 300px;
	background: #eaf3ff;
}

.profile-header{
	background: #eaf3ff;
	padding-top: 20px;
	height: 50%;
}

.profile-usertitle{
	text-align: center;
}

.profile-user-full-name {
	color: #5a7391;
	font-size: 18px;
	font-weight: 600;
}

.profile-user-username {
	color: #5b9bd1;
	font-size: 15px;
	font-weight: 600;
}

.profile-user-bio{
	text-align: center;
	font-size: 14px;
	font-style: italic;
	padding: 5px 20px 10px 20px;
	background: #eaf3ff;
}

.profile-userbuttons {
	text-align: center;
	margin-top: 5px;
}

.profile-userbuttons .btn {
	text-transform: uppercase;
	font-size: 12px;
	font-weight: 600;
	padding: 5px 15px;
	border-radius: 100px;
}

.profile-usermenu{
	/* display: flex;
	flex-direction: column; */
}

.profile-usermenu ul{
	padding: 0;
	list-style: none;
}

.profile-usermenu ul li{
	padding : 0 30px;
	height: 50px;
	color: #5b9bd1;
	font-size: 14px;
	font-weight: 500;
	cursor: pointer;
	display: flex;
	align-items: center;
	border-top: 1px solid #fff;
}

/* .profile-usermenu ul li:hover {
	background-color: #f1faff;
} */



/* card */
.profile-card {
	background: #eaf3ff;
	padding: 10px 30px;
}

.profile-card-header{
	display: grid;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 5px;
	
}

.profile-card-usertitle{
	background: #eaf3ff;
}
.profile-card-user-full-name {
	color: #5a7391;
	font-size: 16px;
	font-weight: 600;
	padding: 0;
	margin: 0;
}

.profile-card-user-username {
	color: #5b9bd1;
	font-size: 14px;
	font-weight: 600;
	padding: 0;
	margin: 0;
}

.profile-card-user-bio{
	font-size: 14px;
	font-style: italic;
	background: #eaf3ff;
}

.profile-card-userbutton {
	text-transform: uppercase;
	font-size: 12px;
	font-weight: 600;
	padding: 6px 15px;
	border-radius: 1000px;
}



@media screen and (max-width: 768px){
	.fixed-div{
		display: none;
	}
}

